.page {
  &__body {
    margin: 0;
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: $colorDark;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform $animationDuration;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 64px;

    @include onDesktop {
      margin-bottom: 104px;
    }
  }

  &__section-title {
    margin-bottom: 0 0 32px;

    font-weight: 700;
    font-size: 36px;
    line-height: 36px;

    text-align: center;

    letter-spacing: -1.5px;
    color: $colorBlack;

    &::after {
      content: "";
      display: block;
      width: 23px;
      height: 16px;
      margin: 4px auto 0;
      background-image: url(../images/crown.svg);
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
    }

    @include onDesktop {
      font-size: 48px;
      line-height: 48px;
      margin-bottom: 56px;
    }
  }
}
