.place {
  &__photo-container {
    position: relative;
    width: 100%;
    max-height: 256px;
    overflow: hidden;

    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }

    @include onDesktop {
      height: 256px;
      padding-bottom: 0;
    }
  }

  &__photos {
    margin-bottom: 16px;

    @include onDesktop {
      margin-bottom: 24px;
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 8px;

    object-fit: cover;
    object-position: center;

    transition: transform $animationDuration;

    @include onDesktop {
      &:hover {
        transform: scale(120%);
      }
    }
  }

  &__link {
    font-weight: 700;
  }
}
